export default class HTMLParser{typeHtmlChars(e,n,r){if(r.contentType!=="html")return n;const t=e.substring(n).charAt(0);if(";"!==t&&"<"!==t)return n;let s="";if(";"===t)s=";";else{s=">";}for(;e.substring(n+1).charAt(0)!==s;){if(n++,n+1>e.length)break;}return n++}backSpaceHtmlChars(e,n,r){if(r.contentType!=="html")return n;const t=e.substring(n).charAt(0);if(";"!==t&&">"!==t)return n;let s="";if(">"===t)s="<";else{s="&";}for(;e.substring(n-1).charAt(0)!==s;){if(n--,n<0)break;}return n--}}export let htmlParser=new HTMLParser();
